import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { CommonService } from 'src/app/service/common.service';
import { TenantService } from 'src/app/service/tenant.service';
import { PasswordValidation } from 'src/app/shared/common';
const pattern = ("(www.)?[a-zA-Z0-9@:%._\\+~#?&//=]{2,256}\\.[a-z]{2,6}\\b([-a-zA-Z0-9@:%._\\+~#?&//=]*)");

@Component({
  selector: 'app-add-tenant',
  templateUrl: './add-tenant.component.html',
  styleUrls: ['./add-tenant.component.scss']
})
export class AddTenantComponent implements OnInit {

  hide = true;
  hide1 = true;
  addForm: FormGroup;
  showLoader = false;
  formErrors = {
    primaryEmailId: null,
    code: null,
    apierror: null,
  };
  tierListArray: Array<any> = [];
  isEditing = false;
  editId = null;
  selected: any;
  selectedFile: any;
  base64textString: string;
  pageTitle = "Add Tenant";
  tenantDetails: any;
  emailRegEx = /^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$/;

  constructor(
    private router: Router,
    private fb: FormBuilder,
    private toastService: ToastrService,
    private tenantService: TenantService,
    private commonService: CommonService,
    private activatedRoute: ActivatedRoute,

  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
      this.isEditing ? this.pageTitle = 'Update Tenant' : this.pageTitle = 'Add Tenant';
    }

    this.addForm = this.fb.group({

      adminLoginId: [null, Validators.compose([Validators.required, Validators.email,])],
      companyName: [null, Validators.compose([Validators.required])],
      primaryContact: [null, Validators.compose([Validators.required, Validators.minLength(8), Validators.maxLength(20)])],
      primaryEmailId: [null, Validators.compose([Validators.required, Validators.pattern(this.emailRegEx)])],
      secondaryContact: ["", Validators.compose([Validators.minLength(8), Validators.maxLength(20)])],
      website: ["", Validators.compose([Validators.required, Validators.pattern(pattern)])],
      secondaryEmailId: ["", Validators.pattern(this.emailRegEx)],
      tierId: [null, Validators.compose([Validators.required])],
      logo: [null, Validators.compose([Validators.required])],
      addressId: [""],
      address: fb.group({
        addressName: [null, Validators.compose([Validators.required])],
        building: [""],
        street: [""],
        locality: [""],
        postalcode: ["", Validators.compose([Validators.required])],
        city: [""],
        state: [""],
        country: ["", Validators.compose([Validators.required])],
        isPrimary: [true]
      }),
      userLicense: [null, Validators.compose([Validators.required])],
      emailTemplate: [''],
      dbConnection: [''],
      code: [null, Validators.compose([Validators.required])],
      new_password: [null, Validators.compose([Validators.required, Validators.minLength(4)])],
      confirm_password: [null, Validators.compose([Validators.required])]
    }, {
      validator: PasswordValidation.passwordValidation()
    });

  }

  ngOnInit() {
    this.getMasterData()
    if (this.isEditing) {
      this.getEditObject()
    }
  }

  getMasterData() {
    this.tenantService.getTiers({
      "searchFilter": null,
      "page": 1,
      "pageSize": 0,
      "fields": null
    }).subscribe((res) => {
      if (res) {
        this.tierListArray = res.data;
      }
    });
  }

  getEditObject() {
    this.tenantService.getTenantById({ id: this.editId }).subscribe((response) => {

      if (response) {
        this.tenantDetails = response.data;
        // this.addForm.patchValue(response.data);

        this.addForm = this.fb.group({
          adminLoginId: response.data.adminLoginId,
          adminPassword: response.data.adminPassword,
          companyName: response.data.companyName,
          primaryContact: response.data.primaryContact,
          primaryEmailId: response.data.primaryEmailId,
          secondaryContact: response.data.secondaryContact,
          website: response.data.website,
          secondaryEmailId: response.data.secondaryEmailId,
          tierId: response.data.tierId,
          isDeleted: response.data.isDeleted,
          tId: response.data.id,
          logo: response.data.logo,
          updatedDate: response.data.updatedDate,
          addressId: response.data.addressId,
          address: this.fb.group({
            addressName: response.miscData.addressName,
            building: response.miscData.building,
            street: response.miscData.street,
            locality: response.miscData.locality,
            postalcode: response.miscData.postalcode,
            city: response.miscData.city,
            state: response.miscData.state,
            country: response.miscData.country,
            isPrimary: response.miscData.isPrimary,
            createdDate: response.miscData.createdDate,
            id: response.miscData.id,
            isDeleted: response.miscData.isDeleted,
            updatedDate: response.miscData.updatedDate,
          }),
          userLicense: [null, Validators.compose([Validators.required])],
          emailTemplate: response.data.emailTemplate,
          dbConnection: response.data.dbConnection,
          code: response.data.code,
          new_password: [null, Validators.compose([Validators.required, Validators.minLength(4)])],
          confirm_password: [null, Validators.compose([Validators.required])]
        })
        this.addForm.patchValue({
          new_password: response.data.adminPassword,
          confirm_password: response.data.adminPassword
        });
        this.onTierChange();
      } else {
        this.router.navigateByUrl('/tenant');
      }
    });
  }

  fileChangeEvent(fileInputFile: any) {
    const reg = /(.*?)\.(jpg|jpeg|png|gif|giff)$/;
    if (!fileInputFile.target.files[0].name.match(reg)) {
      this.toastService.error('Please select valid file');
      this.removeFile();
      return false;
    } else {
      this.removeFile();
      this.selectedFile = fileInputFile.target.files[0];
      this.addForm.get('logo').setValue(this.selectedFile.name);
      // var reader = new FileReader();
      // reader.onload = this.handleReaderLoadedFile.bind(this);
      // reader.readAsBinaryString(this.selectedFile);
    }
  }

  removeFile() {
    this.selectedFile = null;
  }
  // handleReaderLoadedFile(readerEvt) {
  //   var binaryString = readerEvt.target.result;
  //   this.base64textString = 'data:image/png;base64,' + btoa(binaryString);
  // }

  onTierChange() {
    this.tierListArray.map((data) => {
      if (data.id == this.addForm.value.tierId) {
        this.addForm.patchValue({ userLicense: data.licenseCount })
      }
    })
  }

  async submitForm() {
    if (this.addForm.valid) {
      this.showLoader = true;
      if (this.selectedFile) {
        const data = new FormData(); data.append('file', this.selectedFile);
        await this.commonService.fileUpload(data).then((res: any) => { this.addForm.get('logo').setValue(res.data); })
      }
      let data = { ...this.addForm.value, adminPassword: this.addForm.value.new_password, country: this.addForm.value.address.country };
      delete data.userLicense; delete data.new_password; delete data.confirm_password;
      if (this.isEditing) {
        this.tenantService.saveTenant({ ...data, id: this.editId }).subscribe((response) => {
          this.showLoader = false;
          if (response.status == 'Ok') {
            this.router.navigateByUrl('/tenant');
          } else {
            this.toastService.error(response.message)
            response.error.map(obj => {
              if (obj.hasOwnProperty('primaryEmailId')) {
                this.formErrors['primaryEmailId'] = obj['primaryEmailId'];
              } else {
                this.formErrors['apierror'] = `* ${response.error}`;
              }
            });
          }
        }, (error) => {
          this.showLoader = false;
        });

      } else {
        delete data.dbConnection;
        this.tenantService.saveTenant(data).subscribe((response) => {
          this.showLoader = false;
          if (response.status == 'Ok') {
            this.router.navigateByUrl('/tenant');
          } else {
            this.toastService.error(response.message)
            response.error.map(obj => {
              if (obj.hasOwnProperty('primaryEmailId')) {
                this.formErrors['primaryEmailId'] = obj['primaryEmailId'];
              } else {
                this.formErrors['apierror'] = `* ${response.error}`;
              }
            });
          }
        }, (error) => {
          this.showLoader = false;
        });

      }
    }
  }

  onCheckCodeExist() {
    this.tenantService.checkCodeExists(this.addForm.value.code).subscribe((res) => {
      if (res.message == 'Exist') {
        this.addForm.controls['code'].setErrors({ isExit: true });
        this.formErrors['code'] = 'Tenant code already exit';
      } else {
        this.addForm.controls['code'].setErrors(null);
        this.formErrors['code'] = false;
      }
    })
  }

  keyPressAlphaNumerics(event: any) {
    var inp = String.fromCharCode(event.keyCode);
    if (/[0-9+]/.test(inp)) {
      return true;
    } else {
      event.preventDefault();
      return false;
    }
  }
}
